.container
{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.base
{
    width: 100%;
}

.image-container
{
    position: relative;
    width: 1200px;
    max-width: 1200px;
}

.next-button
{
    position: absolute;
    top: 50%;
    right: 0px;
}

.previous-button
{
    position: absolute;
    top: 50%;
    left: 0px;
}